html, body, #root {
  height: 100%;
  width: 100%;
  font-family: sans-serif;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex: 1 1;
          flex: 1 1;
  margin:0;
  padding:0;
}



.drop-down-menu__button {
    background: none;
    border: none;
    cursor: pointer;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    outline: inherit;
}
.drop-down-menu__navlink {
    text-decoration: none;
}
.drop-down-menu__button span {
    font-family: 'Apercu';
    font-size: 20px;
    color: #fff;
}
.drop-down-menu__menu {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    z-index: 1;
    position: absolute;
    border: 2px solid #dadfe8;
    border-radius: 3px;
}
.drop-down-menu__menu__menu-item-wrapper {
    height: 32px;
    width: 250px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    background-color: #f9f9f9;
    cursor: pointer;
}
.drop-down-menu__menu__menu-item-wrapper:hover {
    background-color: #d5e5f2;
}
.drop-down-menu__menu__menu-item {
    font-family: 'Apercu';
    font-size: 18px;
    color: black;
    margin-left: 20px;
}
@font-face {
    font-family: 'EurostileBold';
    src: local('EurostileBold'), url(/static/media/EurostileBold.5c74ade5.ttf) format('truetype');
}
@font-face {
    font-family: 'Apercu';
    src: local('Apercu'), url(/static/media/Apercu.28672a43.ttf) format('truetype');
}

.grid-component {
    height: 300px;
    width: 300px;
    padding: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    border: solid 1px black;
    background-color: #fff;
    border-radius: 6px;
}
.grid-component__title {
    font-size: 24px;
    font-family: 'EurostileBold';
}
.grid-component__description {
    margin-bottom: 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
}
.grid-component__heading {
    color: #ff4242;
    font-family: 'Apercu';
    margin-bottom: 10px;
}
.layout { 
    display: -webkit-flex; 
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex: 1 0 auto;
            flex: 1 0 auto;
}
@font-face {
    font-family: 'Eurostile';
    src: local('Eurostile'), url(/static/media/Eurostile.1175a331.ttf) format('truetype');
}
@font-face {
    font-family: 'Apercu';
    src: local('Apercu'), url(/static/media/Apercu.28672a43.ttf) format('truetype');
}
